.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media(prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.text-warning {
  color: #ffbd39 !important;
}

.btn-warning {
  background-color: #ffbd39 !important;
}
.btn-warning:hover {
  background-color: #ffff;
  color: #000;
  border-color: #fff;
}

.nav .nav-item Link:hover {
  color: #ffbd39 !important;
}
body {
  font-family: monospace;
}

.profileimage {
  border-radius: 55%;
  width: 100%;
  height: 100%;
}

/* .display-2{
  font-size: 4vw;
} */

.big {
  position: absolute;
  font-size: 7vw;
  color: rgba(255, 255, 255, 0.1);
  /* z-index: -1; */
  font-weight: 900;
  top: 0;
}

.bigresume {
  left: 0;
  top: 0;
  right: 0;
}

.about {
  position: relative;
}
.resume {
  position: relative;
}
.about .fs-1 {
  font-size: 4vw !important;
}
.resume .fs-1 {
  font-size: 5vw !important;
}
.about .aboutdesc {
  color: #999999;
}
.resume .aboutdesc {
  color: #999999;
}

.bg-lightshade {
  background-color: rgba(255, 255, 255, 0.1);
  padding: 5%;
}

.color-gray {
  color: #999999;
}

a {
  text-decoration: none;
  color: #000;
}

.about-image {
  border: 10px solid #fff;
  -webkit-box-shadow: 0px 0px 0px 21px rgba(255,189,57,0.7);
  -moz-box-shadow: 0px 0px 19px 21px rgba(255,189,57,0.7);
  box-shadow: -10px -10px 25px 20px rgba(255,189,57,0.7); ;
}

@media (max-width: 576px){
  .about .col-sm-5{
    display: none;
  }
  /* #bannner .col-sm-4{
    display: none;
  } */
}